import React, { Component } from 'react';
import Form from './Form';
import Result from './Result'
import './App.css';


// http://api.openweathermap.org/data/2.5/forecast?q=piła&appid=6a0340dcb344a8dd70d4b2361b8ea65b&units=metric
const ApiKey = '6a0340dcb344a8dd70d4b2361b8ea65b'
// api.openweathermap.org/data/2.5/weather?lat=54.4366596&lon=18.5722921&appid=6a0340dcb344a8dd70d4b2361b8ea65b

class App extends Component {
  state = {
    current: [
      {
        inputValue: '',
        date: '',
        city: '',
        sunrise: '',
        sunset: '',
        temp: '',
        pressure: '',
        wind: '',
        img: [],
        country: '',
        humidity: '',
        description: '',
        error: false
      }
    ],
    forecast: [
      { time: '', temp: '', img: [], day: '' },
      { time: '', temp: '', img: [], day: '' },
      { time: '', temp: '', img: [], day: '' },
      { time: '', temp: '', img: [], day: '' },
      { time: '', temp: '', img: [], day: '' },
    ],
    latitude: ``,
    longitude: ``,
  }



  handleInputChange = (e) => {
    this.setState({
      inputValue: e.target.value
    })
  }

  handleInputSubmit = (e) => {
    e.preventDefault()
    const Api = `https://api.openweathermap.org/data/2.5/weather?q=${this.state.inputValue}&appid=${ApiKey}&units=metric`
    const ForecastApi = `https://api.openweathermap.org/data/2.5/forecast?q=${this.state.inputValue}&appid=${ApiKey}&units=metric`
    // const currentGeoApi = `http://api.openweathermap.org/data/2.5/weather?lat=${this.state.latitude}&lon=${this.state.longitude}&appid=${ApiKey}&units=metric`

    const days = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
    if (!this.state.inputValue) return alert('Wprowadź miasto!')

    fetch(Api)
      .then(response => {
        if (response.ok) return response
        throw Error('Błąd')
      })
      .then(response => response.json())
      // .then(result => console.log(result))
      .then(data => {
        // console.log(data);

        const time = new Date().toLocaleString()
        this.setState(({
          current: [
            {
              inputValue: "",
              error: false,
              date: time,
              city: data.name,
              sunrise: data.sys.sunrise,
              sunset: data.sys.sunset,
              temp: data.main.temp.toFixed(1),
              pressure: `${data.main.pressure} hPa`,
              wind: `${(data.wind.speed * 3.6).toFixed(1)}  km/h`,
              img: `https://openweathermap.org/img/w/${data.weather[0].icon}.png`,
              country: data.sys.country,
              humidity: `${data.main.humidity}%`,
              description: data.weather[0].description.toUpperCase(),
            }]
        }))
      })
      .catch(error => {
        console.log(error)
        this.setState({
          current: [{
            error: true,
            city: this.state.inputValue
          }]
        })
      })


    fetch(ForecastApi)
      .then(response => {
        if (response.ok) return response
        throw Error('Błąd')
      }
      )
      .then(response => response.json())
      // .then(response => console.log(response))
      .then(data => {
        this.setState({
          forecast: [
            {
              time: new Date(data.list[0].dt * 1000).getHours() + ':00',
              day: days[new Date(data.list[0].dt * 1000).getUTCDay()],
              temp: data.list[0].main.temp,
              img: `https://openweathermap.org/img/w/${data.list[0].weather.icon}.png`,
            },
            {
              time: new Date(data.list[1].dt * 1000).getHours() + ':00',
              day: days[new Date(data.list[1].dt * 1000).getUTCDay()],
              temp: data.list[1].main.temp,
              img: `https://openweathermap.org/img/w/${data.list[1].weather.icon}.png`,
            },
            {
              time: new Date(data.list[2].dt * 1000).getHours() + ':00',
              day: days[new Date(data.list[2].dt * 1000).getUTCDay()],
              temp: data.list[2].main.temp,
              img: `https://openweathermap.org/img/w/${data.list[2].weather.icon}.png`,
            },
            {
              time: new Date(data.list[3].dt * 1000).getHours() + ':00',
              day: days[new Date(data.list[3].dt * 1000).getUTCDay()],
              temp: data.list[3].main.temp,
              img: `https://openweathermap.org/img/w/${data.list[3].weather.icon}.png`,
            },
            {
              time: new Date(data.list[4].dt * 1000).getHours() + ':00',
              day: days[new Date(data.list[4].dt * 1000).getUTCDay()],
              temp: data.list[4].main.temp,
              img: `https://openweathermap.org/img/w/${data.list[4].weather.icon}.png`,
            },

          ]
        })

      })
      .catch(error => {
        console.log(error)
        this.setState({
          current: [{
            error: true,
            city: this.state.inputValue
          }]
        })
      })
  }
  handleGetLocation = () => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition((position) => {
        this.setState({
          latitude: position.coords.latitude,
          longitude: position.coords.longitude,
        })
      });
    }
    else {
      console.log("Geolocation is not supported by this browser.");
    }
  }
  componentDidMount() {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition((position) => {
        this.setState({
          latitude: position.coords.latitude,
          longitude: position.coords.longitude,
        })
      });
    }
    else {
      console.log("Geolocation is not supported by this browser.");
    }

    // if (this.state.longitude) {
    //   this.showFormLoc()
    // }
  }
  componentDidUpdate() {
    const currentGeoApi = `https://api.openweathermap.org/data/2.5/weather?lat=${this.state.latitude}&lon=${this.state.longitude}&appid=${ApiKey}&units=metric`
    const forecastGeoApi = `https://api.openweathermap.org/data/2.5/forecast?lat=${this.state.latitude}&lon=${this.state.longitude}&appid=${ApiKey}&units=metric`
    const days = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
    if (this.state.latitude && this.state.longitude) {
      fetch(currentGeoApi)
        .then(response => {
          if (response.ok) return response
          throw Error('Błąd')
        })
        .then(response => response.json())
        // .then(result => console.log(result))
        .then(data => {
          // console.log(data);
          const time = new Date().toLocaleString()
          this.setState({
            current: [
              {
                inputValue: "",
                error: false,
                date: time,
                city: data.name,
                sunrise: data.sys.sunrise,
                sunset: data.sys.sunset,
                temp: data.main.temp.toFixed(1),
                pressure: `${data.main.pressure} hPa`,
                wind: `${(data.wind.speed * 3.6).toFixed(1)}  km/h`,
                img: `https://openweathermap.org/img/w/${data.weather[0].icon}.png`,
                country: data.sys.country,
                humidity: `${data.main.humidity}%`,
                description: data.weather[0].description.toUpperCase(),
              }],
            latitude: '',
            longitude: '',
          })
        })
        .catch(error => {
          console.log(error)
        })


      fetch(forecastGeoApi)
        .then(response => {
          if (response.ok) return response
          throw Error('Błąd')
        }
        )
        .then(response => response.json())
        // .then(response => console.log(response))
        .then(data => {
          this.setState({
            forecast: [
              {
                time: new Date(data.list[0].dt * 1000).getHours() + ':00',
                day: days[new Date(data.list[0].dt * 1000).getUTCDay()],
                temp: data.list[0].main.temp,
                img: `https://openweathermap.org/img/w/${data.list[0].weather.icon}.png`,
              },
              {
                time: new Date(data.list[1].dt * 1000).getHours() + ':00',
                day: days[new Date(data.list[1].dt * 1000).getUTCDay()],
                temp: data.list[1].main.temp,
                img: `https://openweathermap.org/img/w/${data.list[1].weather.icon}.png`,
              },
              {
                time: new Date(data.list[2].dt * 1000).getHours() + ':00',
                day: days[new Date(data.list[2].dt * 1000).getUTCDay()],
                temp: data.list[2].main.temp,
                img: `https://openweathermap.org/img/w/${data.list[2].weather.icon}.png`,
              },
              {
                time: new Date(data.list[3].dt * 1000).getHours() + ':00',
                day: days[new Date(data.list[3].dt * 1000).getUTCDay()],
                temp: data.list[3].main.temp,
                img: `https://openweathermap.org/img/w/${data.list[3].weather.icon}.png`,
              },
              {
                time: new Date(data.list[4].dt * 1000).getHours() + ':00',
                day: days[new Date(data.list[4].dt * 1000).getUTCDay()],
                temp: data.list[4].main.temp,
                img: `https://openweathermap.org/img/w/${data.list[4].weather.icon}.png`,
              },

            ]
          })

        })
        .catch(error => {
          console.log(error)
          this.setState({
            current: [{
              error: true,
              city: this.state.inputValue
            }]
          })
        })
    }
  }


  render() {
    return (
      <div className="App">
        <h1>Weather App</h1>
        <Form
          value={this.state.current.inputValue}
          change={this.handleInputChange}
          submit={this.handleInputSubmit}
          location={this.handleGetLocation}
          show={this.showFormLoc} />
        <Result data={this.state} />
      </div>
    );
  }
}

export default App;
